.address {
  & h3 {
    font-weight: 500;
    font-size: 18px;
    line-height: 27px;
    margin: 0 0 31px 0;

    @include vp-1023 {
      margin: 0 0 27px 0;
    }
  }
}

.address__list {
  display: flex;
  flex-direction: column;
  gap: 22px;
  font-style: normal;

  & p,
  a {
    margin: 0;
    padding-left: 25px;
    position: relative;

    @include vp-767 {
      padding-left: 27px;
    }

    &::before {
      position: absolute;
      top: 0;
      left: 0;
      width: 18px;
    }
  }
}

.address__list p::before {
  content: url("../img/svg/pin-icon.svg");
  height: 22px;

  @include vp-1023 {
    top: 5px;
  }
}

.address__list a::before {
  content: url("../img/svg/phone.svg");
  height: 18px;
  top: 2px;
}

.address__list a {
  transition: opacity $trans-default;

  @include hover-focus {
    opacity: 0.8;
  }
}
