.intro {
  padding: 190px 0 278px;

  @include vp-1023 {
    padding: 210px 0 273px;
  }

  @include vp-767 {
    padding: 124px 0 180px;
  }

  & h2 {
    margin: 0 0 12px 0;
    color: $color-alice-blue;
    font-weight: 700;
    font-size: 54px;
    line-height: 66px;
    text-shadow: 0 4px 4px rgba(0, 0, 0, 0.25);

    @include vp-1023 {
      margin: 0 0 15px 0;
      font-size: 50px;
      line-height: 61px;
      text-shadow: none;
    }

    @include vp-767 {
      margin: 0 0 10px 0;
      font-size: 36px;
      line-height: 45px;
    }
  }

  & p {
    margin: 0;
    color: $color-alice-blue;
    font-size: 20px;
    text-shadow: 0 4px 4px rgba(0, 0, 0, 0.25);

    @include vp-1023 {
      font-size: 18px;
      line-height: 22px;
      text-shadow: none;
    }

    @include vp-767 {
      max-width: 230px;
      font-size: 16px;
    }
  }
}

.is-nojs .intro {
  @include vp-767 {
    padding: 40px 0 185px;
  }
}

.intro__wrapper {
  padding-left: 95px;
  max-width: 975px;

  @include vp-1023 {
    padding: 0;
    max-width: 100%;
  }
}
